.badge {
  display: inline-block;
  width: 10px;
  height: 10px;
  border-radius: 30px;

  &--green {
    background-color: #42b883;
  }

  &--blue {
    background-color: #64c4ed;
  }

  &--pink {
    background-color: #ffbbcc;
  }

  &--greening {
    background-color: #b6e6bd;
  }

  &--gree {
    background-color: #c9d1d3;
  }
}
